.js-style {
  display: none;
}

.loader {
  width: 48px;
  height: 48px;
  background: #fff;
  border-radius: 50%;
  animation: roll 1s ease-in-out infinite alternate;
  position: relative;
}

.loader:after {
  content: "";
  border: 5px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

@keyframes roll {
  0% {
    transform: translateX(-150%)rotate(0deg);
  }

  100% {
    transform: translateX(150%)rotate(360deg);
  }
}

.card-info {
  padding-left: 20px;
  padding-right: 20px;
  display: block;
}

.cat-info {
  padding-top: 20px;
  display: flex;
}

.photo {
  box-shadow: 10px 1px 5px #7c7777;
}

